import React, { Component } from "react";
import Widecard from "../components/Widecard";

class Education extends Component {
  render() {
    return (
      <div className="condiv">
        <h1 className="subtopic">My Education</h1>
        <Widecard
          title="S.S.C."
          where="New English School Deolali Pravara"
          from="Passing Year"
          to="2013"
          aggregate="80.18%"
        />
        <Widecard
          title="H.S.C."
          where="Arts,Sience and Commerce College Rahuri"
          from="Passing Year"
          to="2015"
          aggregate="59.23%"
        />
        <Widecard
          title="B.Sc.(Electronics)"
          where="Padmashri Vikhe Patil College Pravaranagar"
          from="2015"
          to="2018"
          aggregate="81.08%"
        />
        <Widecard
          title="M.Sc.
          Instrumentation Science"
          where="Savitribai Phule Pune University"
          from="2017"
          to="Current"
        />
      </div>
    );
  }
}

export default Education;
