import React, { Component } from "react";
import ReactTypingEffect from "react-typing-effect";
import profilepic from "../img/saurabh.jpeg";

class Home extends Component {
  render() {
    return (
      <div className="condiv home">
        <img src={profilepic} alt="profile_pic" className="profilepic"></img>
        <ReactTypingEffect
          className="typingeffect"
          text={[
            "I am Saurabh Dilip Tanpure",
            "I am an Electrical Science student",
          ]}
          speed={100}
          eraseDelay={500}
        />
      </div>
    );
  }
}

export default Home;
